<template>
	<div style="margin: 80px 0 0 10px;">
		<div id="container"></div>
	</div>
</template>

<script>
	import { Bar } from '@antv/g2plot';
	export default {
		data() {
			return {
				dataSource: [],
			};
		},
		methods: {
			async getActivateData() {
				let {
					data
				} = await this.$get(this.$store.state.requestPath + '/admin/activate_user/chart');
				this.dataSource = data.data;

				console.log(this.dataSource)
			},
		},
		async created() {
			this.getActivateData();
		},
		watch: {
			dataSource: async function(data) {
				const barPlot = new Bar('container', {
				  data,
				  xField: 'ecount',
				  yField: 'name',
				  barWidthRatio: 0.3,
				  meta: {
				    name: {
				      alias: '用户名',
				    },
				    ecount: {
				      alias: '活跃量',
				    },
				  },
				});		
				barPlot.update({ "theme": { "styleSheet": { "brandColor": "#9DF5CA", "paletteQualitative10": ["#9DF5CA", "#61DDAA", "#42C090", "#19A576", "#008A5D", "#006F45", "#00562F", "#003E19", "#002800"], "paletteQualitative20": ["#9DF5CA", "#61DDAA", "#42C090", "#19A576", "#008A5D", "#006F45", "#00562F", "#003E19", "#002800"] } } });
				barPlot.render();
			}
		},

	}
</script>

